import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import { Menu, Sidebar } from 'semantic-ui-react';

const SideBar = () => {
    const history = useHistory();

    const redirectTo = (link) => { history.push(link) }

    return(
            <Menu pointing vertical>
                <Menu.Item key='user' onClick={() => redirectTo('/stats-dashboard')}>
                    <Link to='/stats-dashboard'>User</Link>
                </Menu.Item>
                <Menu.Item onClick={() => redirectTo('/stats-dashboard/study/add')}>
                    <Link to='/stats-dashboard/study/add' >add study</Link>
                </Menu.Item>
            </Menu>
    )
}

export default SideBar;